import { useContext, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Breadcrumb, Button, message, Typography, Upload } from "antd";
import { Card } from "../../Components";
import withAuth from "../../HOC/withAuth";
import { FiUpload } from "react-icons/fi";
import { labelColor } from "../../Utils/colors";
import { endpoint } from "../../Utils/enpoint";
import axios from "axios";
import { AuthContext } from "../../Store/AuthContext";

const { Title, Text } = Typography;
const { Dragger } = Upload;

const EmployeeUpload = () => {
  const [fileUpload, setFileUpload] = useState([]);
  const [loading, setLoading] = useState(false);

  const authContext = useContext(AuthContext);
  const navigate = useNavigate();

  const handleUpload = async () => {
    setLoading(true);
    if (fileUpload.length === 0) {
      message.warn("Pilih file terlebih dahulu!");
      setLoading(false);

      return null;
    }

    let request = [];

    await [...fileUpload].map((item) => {
      const dataPost = new FormData();
      dataPost.append("uploaded_file", item);

      return request.push(
        axios
          .post(`${endpoint}/api/employee/upload`, dataPost, {
            headers: {
              "Content-Type": "multipart/form-data",
              Authorization: `bearer ${authContext.userToken}`,
            },
          })
          .then((response) => {
            if (!response) {
              throw new Error();
            }

            // if everything is fine resiolve to true or for example your body content
            console.log(`response post: `, response.data);
            setFileUpload((prev) =>
              prev.filter((file) => file.uid !== item.uid)
            );
            return Promise.resolve(true);
          })
          .catch((e) => {
            // if we encounter an error resolve to false
            if (e.response) {
              console.log("error response:", e.response.data.error);
            } else {
              console.log("there was an error:", e);
            }
            return Promise.resolve(false);
          })
      );
    });

    console.log("Left For Loop");

    await Promise.all(request).then((results) => {
      // here we have all the results
      console.log("all request finished!", results);
      const resultFilter = [...results].filter((data) => data === true);

      console.log("resultFilter: ", resultFilter);

      // handle based response submit approval server
      if (resultFilter.length !== fileUpload.length) {
        message.warn("Sebagian file tidak terupload");
        setLoading(false);

        return null;
      }

      message.success("Sukses upload file");
      setLoading(false);
      navigate("/user-management/employee");
    });
  };

  return (
    <div>
      <Breadcrumb style={{ margin: "16px 0" }}>
        <Breadcrumb.Item
          style={{ cursor: "pointer" }}
          onClick={() => navigate("/user-management")}
        >
          User Management
        </Breadcrumb.Item>
        <Breadcrumb.Item
          style={{ cursor: "pointer" }}
          onClick={() => navigate("/user-management/employee")}
        >
          Employee
        </Breadcrumb.Item>
        <Breadcrumb.Item>Upload Employee</Breadcrumb.Item>
      </Breadcrumb>
      <div>
        <Card>
          <Title
            level={3}
            type="secondary"
            style={{ marginRight: 20, marginBottom: 30 }}
          >
            Upload Employee
          </Title>
          <Dragger
            name="file"
            height={200}
            beforeUpload={(file) => {
              setFileUpload([...fileUpload, file]);
              return false;
            }}
            onRemove={(file) =>
              setFileUpload((prev) =>
                prev.filter((item) => item.uid !== file.uid)
              )
            }
          >
            <div>
              <FiUpload size={30} color={labelColor} />
            </div>
            <Text type="secondary">
              Click or drag file to this area to upload
            </Text>
          </Dragger>
          <Button
            type="primary"
            shape="round"
            icon={<FiUpload />}
            size="large"
            style={{ marginTop: 20 }}
            onClick={handleUpload}
            loading={loading}
          >
            Upload
          </Button>
        </Card>
      </div>
    </div>
  );
};

export default withAuth(EmployeeUpload);
